import { styled } from "@mui/material"
import { CLASS_NAMES } from "../../constants"
import { HighlightItem, generateRpJsonStylesheet } from "../../styles/generate-rpjson-stylesheet"

type Props = {
  highlights: Array<HighlightItem>
  showParagraphsNumbers?: boolean
  showSentencesNumbers?: boolean
  originalBreakLines?: boolean
}

const RpJsonAnnotationStyles = styled("div", {
  name: "RpJsonAnnotationStyles",
  shouldForwardProp: prop =>
    prop !== "highlights" &&
    prop !== "showParagraphsNumbers" &&
    prop !== "showSentencesNumbers" &&
    prop !== "originalBreaklines",
})<Props>(
  ({
    highlights,
    showParagraphsNumbers = false,
    showSentencesNumbers = false,
    originalBreakLines = false,
  }) => {
    const formattedHighlights = generateRpJsonStylesheet(highlights)

    return {
      "&": {
        wordWrap: "break-word",
      },
      // Options Styles
      [`& .${CLASS_NAMES.PARAGRAPH_NUMBER}`]: {
        display: showParagraphsNumbers ? "initial" : "none",
      },
      [`& .${CLASS_NAMES.SENTENCE_NUMBER}`]: {
        display: showSentencesNumbers ? "initial" : "none",
      },
      [`& br`]: {
        display: originalBreakLines ? "initial" : "none",
      },

      // Highlight styles
      ...formattedHighlights,
    }
  },
)

export { RpJsonAnnotationStyles }
